.j-plugin-slider{position:relative;padding:0.625rem 0.938rem;width:100%;height:100%}.j-plugin-slider__path{border-radius:0.313rem;width:100%;max-width:15.625rem;height:0.313rem;background:#f0f0f0;position:relative}.j-plugin-slider_vertical{padding-top:1.250rem;padding-bottom:1.250rem}.j-plugin-slider_vertical .j-plugin-slider__path{min-height:15.625rem;max-width:0.313rem}.j-plugin-slider_vertical.j-plugin-slider_with-point{padding-right:2.500rem;padding-top:1.250rem}.j-plugin-slider_vertical .j-plugin-slider__thumb{transform:translateY(-50%) translateX(-35%)}.j-plugin-slider_vertical .j-plugin-slider__range{width:100%}.j-plugin-slider_vertical .j-plugin-slider__follower-point{top:0;transform:translateX(1.563rem)}.j-plugin-slider_vertical .j-plugin-slider__follower-point::after{top:30%;left:0px}.j-plugin-slider_with-point{padding-top:2.500rem}.j-plugin-slider__thumb{width:1.188rem;height:1.188rem;border-radius:50%;position:relative;transform:translateY(-35%) translateX(-50%);background:#f04e32;cursor:pointer;position:absolute;z-index:0;box-sizing:border-box}.j-plugin-slider__thumb_selected{z-index:1;background:#da3131}.j-plugin-slider__thumb_selected .j-plugin-slider__follower-point{background:#da3131}.j-plugin-slider__thumb_selected .j-plugin-slider__follower-point::after{background:#da3131}.j-plugin-slider__thumb:hover{z-index:2}.j-plugin-slider__range{height:0.313rem;position:absolute;background:#ffd3c9}.j-plugin-slider__follower-point{min-width:1.875rem;height:1.125rem;position:absolute;padding-top:0.125rem;padding-left:0.500rem;padding-right:0.500rem;top:-1.563rem;left:50%;transform:translateX(-50%);background:#f04e32;color:white;font-size:0.750rem;border-radius:0.250rem;text-align:center;box-sizing:border-box}.j-plugin-slider__follower-point::after{content:"";position:absolute;width:0.438rem;height:0.438rem;border-radius:0 0 1px 0;background-color:#f04e32;left:50%;transform:translateX(-50%) rotate(45deg);bottom:-0.188rem}
